<template>
    <article class="service" v-if="svc">
        <ul class="links">
            <li v-if="svc.link.home"><a :href="svc.link.home" title="GÃ¥ til tjenesten"><fa-icon name="external-link"/></a></li>
            <li v-if="svc.link.doc"><a :href="svc.link.doc" title="Dokumentasjon av tjenesten"><fa-icon name="question-circle"/></a></li>
            <li v-if="bus.ita_mode"><a :href="svc.link.href_tk + '/edit'" title="Rediger info om tjenesten (krever innlogging)"><fa-icon name="pencil-square-o"/></a></li>
        </ul>
        <div class="kicker"><fa-icon name='cog'/> Tjeneste</div>
        <h1>{{svc.title}}</h1>
        <p v-if="'summary' in svc.text" class="lead">{{svc.text.summary}}</p>
        <p v-if="'description' in svc.text" class="desc">{{svc.text.description}}</p>

        <div class="fields">
            <field v-if="svc.related && svc.related.service_owner" name="Systemeier">
                {{svc.related.service_owner.title}}
            </field>
            <field v-if="svc.related && svc.related.support_contact" name="BrukerstÃ¸tte">
                {{svc.related.support_contact.title}}
            </field>
            <field v-if="bus.ita_mode" name="Normalt tilgjengelig">
                {{svc.info.availability}}
            </field>
            <field v-if="bus.ita_mode" name="Tjenestetype">
                {{svc.info.service_type}}
            </field>
            <field v-if="bus.ita_mode" name="Kritikalitet">
                {{svc.info.criticality}}
            </field>
            <field v-if="bus.ita_mode" name="Informasjonsklasse">
                {{svc.info.information_class}}
            </field>
            <field v-if="bus.ita_mode" name="Livssyklus">
                {{svc.info.service_state}}
            </field>
            <field v-if="bus.ita_mode" name="Vaktordning">
                {{svc.info.service_rotation}}
            </field>
            <field v-if="svc.info.service_supplier" name="DriftsleverandÃ¸r">
                {{svc.info.service_supplier}}
            </field>
            <field v-if="bus.ita_mode && svc.related && svc.related.op_team" name="Driftsteam">
                {{svc.related.op_team.title}}
            </field>
            <field v-if="bus.ita_mode" name="Registreringsstatus">
                {{svc.info.edit_state}}
            </field>
        </div>

        <div v-if="bus.ita_mode" class="meta">
            Opprettet {{ svc.meta.created.substr(0,10) }}
            av <user v-bind="svc.meta.author"/>,
            oppdatert {{ svc.meta.changed.substr(0,10) }}.
        </div>
    </article>
    <div v-else><p v-if="err">{{err}}</p></div>
</template>

<script>
import FaIcon from '@/components/FaIcon.vue';
import Field from '@/components/Field.vue';
import User from '@/components/User.vue';
import { eventBus } from '@/main';
import tkdata from '@/tkdata';

export default {
    name: 'Service',
    props: {
        svcId: { type: String, required: true }
    },
    data() {
        return {
            svc: null,
            bus: eventBus,
            err: null,
        }
    },
    created() {
        tkdata().then(data => {
            const svc = data.service[this.svcId];
            if (!svc)
                this.err = '404 Tjenesten finnes ikke';
            this.svc = svc;
        });
    },
    components: {
        FaIcon,
        Field,
        User
    }
}
</script>

